<template>
  <div class="col-12 no_x" v-if="ready">
      <div class="col-12" style="display:flex;flex-direction:row">
            <div class="col-5">
              <div>
                 Welcome
                    <br/> 
                    <b>{{ name }}</b>
                    <br/>
                    Please Sign Here <i class="fa fa-arrow-down"></i>
              </div>
                    <VueSignaturePad
                    id="signature"
                    width="100%"
                    height="200px"
                    ref="signaturePad"
                    />
                    <div style='margin-top:0.5em;margin-left:30%;margin-right:30%'>
                        <button class="btn btn-outline-secondary float-right" @click="undo">
                            Undo
                        </button>
                      <button class="btn btn-outline-primary float-left" @click="save">
                          Sign
                      </button>
                  </div>   
            </div>  
            <div  class="col-7"><iframe :src="url" style="background: #FFFFFF;width:100%;height:87vh;"></iframe>
          <!---->                
        </div>
      </div>
    </div>
</template>

<script>
function getURLParameter(name) {
  return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.search) || [null, ''])[1].replace(/\+/g, '%20')) || null;
}
export default {
    data(){
        return{
            rank:'',
            r_id:'',
            name:'',
            userlist:[],
            proc_id:'',
            url:'',
            ready:false,
            params: window.location.href.substr(window.location.href.indexOf('?')),
        }
    },
async created(){
    let param = new URLSearchParams(this.params);
     if(param.has('id')){
        this.r_id = param.get('id');
        this.proc_id = param.get('proc_id');
        console.log(this.proc_id);      
        }      
    var baseurl = "src/Procurement/procurement/backend/declaration_form.php?proc_id=";
    this.url = baseurl + this.proc_id;
    //onload data
    var formData = new FormData();
    formData.append('record',this.r_id);
    await axios.post('Procurement/procurement/backend/attend_fetch_live.php',formData)
        .then(
            (response)=>{
                this.userlist = response.data;
                this.name = this.userlist[0].name;
                this.rank = this.userlist[0].rank;
            }
        )


    this.ready = true;
},
    
  name: 'user_setup',
  methods: {
    undo() {
      this.$refs.signaturePad.undoSignature();
      this.pwd='';
      this.pwd_check='';
    },
    save() {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
      if(isEmpty){
          this.$alert('Signature board cannot be empty!');
        } else {
          var sign = data;
          var formData = new FormData();  
          formData.append('sign',sign);
          formData.append('rid',this.r_id);   
          axios.post('Procurement/procurement/backend/live_sign_attend_post.php',formData)
            .then(
              (response)=>{
                  this.$alert('Successfully Signed, return to previous form ...');
                  document.getElementById('tmp_id').value = this.proc_id;
                  this.$router.push('tender_meeting_mat');
              }
            )
        }     
    }
  }
};
</script>

<style>
#signature {
  border: double 3px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
    radial-gradient(circle at top left, #4bc5e8, #9f6274);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
</style>



